@use 'prismjs/themes/prism-tomorrow.min';

// spacers
$gap-s: 5px;
$gap-m: 10px;
$gap-l: 20px;

// main content
$content-max-width: 960px;
$content-padding-x: 50px;

// footer
$footer-text-color: #b1c4d0;
$footer-bg-color: #15384f;

// override default styles of prismjs
pre[class*="language-"] {
  padding: 5px 10px;
}
pre[class*="language-"],
code[class*="language-"] {
  font-size: 13px;
  line-height: 1.3;
}

:not(pre) > code,
:not(pre) > code[class*="language-"] {
  padding: 0.1em 0.4em;
}

body {
  margin: 0;
  padding: 0;
  font-family: Arial, sans-serif;
  line-height: 1.3;
}

a {
  color: #235e84;
  text-decoration: none;

  &.link {
    color: #1993ff;
  }

  &:hover {
    text-decoration: underline;
  }
}

code a {
  color: #aaa;
  text-decoration: underline;

  &:hover {
    color: #95cef8;
  }
}

// placeholder for responsive image in header
.header-container {
  width: 100%;
  height: 300px;
  max-height: 300px;
  overflow: hidden;
  background-size: cover;
  // placeholder of responsive image
  // 2 ways to define placeholder: one is in Pug via style attribute, other is in CSS
  background-image: url('Images/abstract.webp?prop=placeholder&placeholder=true&placeholderSize=100');
  background-position: 0 35%;

  // responsive image
  img {
    width: 100%;
    height: 100%;
    object-fit: cover;
    object-position: 50% 40%;
  }
}

.header-container {
  color: white;

  .header-content {
    position: relative;
    display: flex;
    max-width: $content-max-width;
    margin-left: auto;
    margin-right: auto;
    padding-left: $content-padding-x;
    padding-right: $content-padding-x;
    justify-content: center;
  }

  .text {
    position: absolute;
    width: fit-content;
    font-size: 40px;
    color: #d6b729;
    text-transform: uppercase;
    z-index: 10;

    @media (max-width: 760px) {
      font-size: 30px;
    }
    @media (max-width: 360px) {
      font-size: 24px;
    }
  }
}

.main-container {
  display: flex;
  flex: 1 0 auto;
}

.main-content {
  display: flex;
  flex-direction: column;
  height: 100%;
  //width: 100%;
  max-width: $content-max-width;
  margin-left: auto;
  margin-right: auto;
  padding-left: $content-padding-x;
  padding-right: $content-padding-x;
}

section {
  &:first-of-type {
  margin-top: $gap-l;
  }
  .container {
    padding: $gap-l $gap-l * 1.5;
    background-color: #fff;
  }
}

table,
td {
  border: 0;
  border-spacing: 0;
  border-collapse: collapse;

  pre[class*="language-"] {
    margin: 0;
  }
}

table.image-preview {
  td {
    vertical-align: top;
    padding: 0;
  }
  td:first-of-type {
    text-align: right;
  }
  td:last-of-type {
    padding: 0 0 0 10px;
  }
}

ul {
  margin: 5px 0 0 0;
}

.copyleft {
  &:before {
    content: '\00a9';
  }
  font-size: 18px;
  display: inline-block;
  transform: scaleX(-1);
}

.footer-container {
  margin-top: $gap-l;
  padding: 30px 50px;
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  flex-shrink: 0;
  color: $footer-text-color;
  background-color: $footer-bg-color;

  > :not(:first-child) {
    margin-left: $gap-m;
  }

  a {
    color: $footer-text-color;
    text-decoration: underline;
  }
}

// styles for demo on page
$width: 200;
$height: 145;
.demo-image {
  width: #{$width}px;
  height: #{$height}px;
  background-image: url('Images/image.webp?size=#{$width}');
}
